* {
  --primary: #d81e5b;
  --secondary: #8a4efc;
  --light: #eee;
  --light-alt: #61759b;
  --dark: #131a26;
  --dark-alt: #202b3e;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

body {
  background-color: var(--dark-alt);
  color: var(--light);
}

.App {
  padding: 2rem;
}

h1 {
  margin-bottom: 1rem;
}

h4 {
  text-transform: uppercase;
  color: var(--light-alt);
}

.todo {
  position: relative;
  display: flex;
  padding: 1rem;
  background-color: var(--dark);
  border-radius: 0.5rem;
  margin: 1rem 0rem;
  transition: 0.2s;
  cursor: pointer;
  align-items: center;
}

.todo:hover {
  opacity: 0.8;
}

.todo .checkbox {
  width: 16px;
  height: 16px;
  margin-right: 16px;
  border-radius: 10px;
  background-color: var(--dark-alt);
  transition: 0.4s;
}

.todo.is-complete .checkbox {
  background-color: var(--primary);
  background-image: linear-gradient(to bottom, var(--primary), var(--secondary));
}

.todo.is-complete .text {
  text-decoration: line-through;
}

.todo .delete {
  border-radius: 50%;
  color: var(--light);
  position: absolute;
  right: 16px;
}

.todo .delete img {
  width: 32px;
}

.todo .delete:active {
  transform: scale(0.75);
}

.new-todo input {
  width: 100%;
  font-size: 1rem;
  padding: 0.5rem;
  margin: 1rem 0rem 0rem 0rem;
  background-color: var(--light);
}

.new-todo button {
  width: 120px;
  height: 32px;
  font-size: 1rem;
  margin: 1rem 0rem;
  background-color: #d4ff6f;
  cursor: pointer;
}

.new-todo button:active {
  transform: scale(0.99);
}